<template>
  <v-content>
    <v-container fill-height>
      <v-layout row wrap align-center>
        <v-flex xs12 md4 align-center >
          <div class="text-xs-center">
            <v-avatar size="125px">
              <img
                class="img-circle elevation-7 mb-1"
                src="https://avatars2.githubusercontent.com/u/4192023?s=460&v=4"
              >
            </v-avatar>
            <div class="headline ">Adam Valair</div>
            <div class="job-title text-xs-center grey--text text--darken-1 pt-2 pb-4" >Developer & Spatial Analyst</div>
            <v-layout justify-center>
              <a target="_blank" href="https://github.com/spatialbits" class="mr-5 body-2">
                <icon name="github" label="GitHub" scale="1.25" class="grey--text text--darken-2"></icon>
              </a>
              <a target="_blank" href="https://www.linkedin.com/in/adam-valair-a4316b25/" class="body-2">
                <icon name="linkedin" label="LinkedIn" scale="1.25" class="grey--text text--darken-2"></icon>
              </a>
              <a href="mailto:adam@bitspatial.com" class="ml-5 body-2">
                <icon name="envelope" label="Email" scale="1.25" class="grey--text text--darken-2"></icon>
              </a>
            </v-layout>
            <v-tabs v-if="show_tabs" v-model="current_tab_index" fixed-tabs class="pt-4" > <!-- TODO fix broken hack that sets tab background manually -->
              <v-tab v-for="item in info_tabs" :key="info_tabs[item]" :to="{name:item.title}">
                {{ item.title }}
              </v-tab>
            </v-tabs>
            <br/><br/>
          </div>
        </v-flex>
        <v-flex xs12 md7 offset-md1 align-center >
        <!--<v-flex align-center >-->
          <transition name="fade">
            <router-view/>
          </transition>
        </v-flex>
      </v-layout>
    </v-container>
    <v-footer class="mb-3" fixed>
      <v-spacer></v-spacer>
      <div>
        <v-tooltip left>
          <logo slot="activator" class="mb-5" style="width:22px;"></logo>
          <span>Adam Valair</span>
        </v-tooltip>
      </div>
    </v-footer>
  </v-content>
</template>
<style>
  /*Stupid tabs bar background is wrong.*/
  div.tabs__bar {
    background-color: #fafafa !important;
  }
  footer {
    background-color: #fafafa !important;
  }
  .job-title {
    font-size: 20px !important;
    line-height: 1 !important;
    letter-spacing: .01em !important;
  }
</style>
<script>
  import 'vue-awesome/icons/github'
  import 'vue-awesome/icons/linkedin'
  import 'vue-awesome/icons/envelope'
  import Icon from 'vue-awesome/components/Icon'
  import Projects from '@/components/Projects'
  import Logo from '@/components/Logo'

  export default {
    name: 'App',
    components: {
      Icon,
      Projects,
      Logo
    },
    data () {
      return {
        show_tabs: false,
        current_tab_index: 1,
        info_tabs: [
          {title: 'Projects'},
          {title: 'Background'}]
      }
    }
  }
</script>
<!--<div style="height:90vh; padding-right:20px;">-->



