import {HTTP} from '@/services/common'

export default {
  login: function (username, password) {
    return HTTP.post(
      '/auth/login',
      {
        username: username,
        password: password
      }
    ).then(response => {
      console.log('Successful login, store the access, refresh tokens', response.data.access_token, response.data.refresh_token)
      this.setAccessToken(response.data.access_token)
      this.setRefreshToken(response.data.refresh_token)
      return response  // return the original response so we can do something with it later.
    }).catch(err => {
      console.log('Unsuccessful login. Response:', err)
      this.removeAccessToken()
      this.removeRefreshToken()
      return Promise.reject(err)  // return the original caught response so we can do something with it later.
    })
  },
  logout: function () {
    this.removeAccessToken()
    this.removeRefreshToken()
  },
  checkAccessToken () {
    let config = {
      headers: {
        Authorization: 'Bearer ' + this.getAccessToken()
      }
    }
    return HTTP.get('/auth/token-check', config)
  },
  setAccessToken: function (token) {
    localStorage.setItem('access_token', token)
  },
  getAccessToken: function () {
    return localStorage.getItem('access_token') || false
  },
  removeAccessToken: function () {
    return localStorage.removeItem('access_token')
  },
  setRefreshToken: function (token) {
    localStorage.setItem('refresh_token', token)
  },
  getRefreshToken: function () {
    return localStorage.getItem('refresh_token') || false
  },
  removeRefreshToken: function () {
    return localStorage.removeItem('refresh_token')
  },
  refreshAccessToken: function () {
    let config = {
      headers: this.getRefreshHeader()
    }
    return HTTP.post('/auth/refresh', {}, config)
      .then(response => {
        console.log('refresh token response:', response)
        this.setAccessToken(response.data.access_token)
      })
  },
  getAuthorizationHeader () {
    let token = this.getAccessToken()
    if (!token) {
      return {}
    } else {
      return {
        Authorization: 'Bearer ' + token
      }
    }
  },
  getRefreshHeader () {
    let token = this.getRefreshToken()
    if (!token) {
      return {}
    } else {
      return {
        Authorization: 'Bearer ' + token
      }
    }
  },
  hasTokens: function () {
    if (!!this.getAccessToken() && !!this.getRefreshToken()) {
      return true
    } else {
      return false
    }
  },
  authGuard: function (to, from, next) {
    /* checks tokens in localstorage against the server and redirects as necessary.
    *  This is deprecated but left in because it might be handy.
    *  Instead, the router.beforeEach uses hasTokens() and redirects on that.
    *  Permission errors are handled on ajax responses.
    * */
    // check for valid auth token
    this.checkAccessToken()
      .then(response => {
        console.debug('server says access token is valid:', response)
        // Token is valid, so continue
        next()
      })
      .catch(err => {
        console.debug('access token bad or expired', err)
        // either the access token is bad or expired - try to refresh:
        if (err.response.status === 422 || err.response.status === 401) {
          this.refreshAccessToken()
            .then(response => {
              console.debug('token refresh successful.')
              next()
            })
            .catch(err => {
              console.error('unable to refresh access token, redirect to login.', err)
              next({
                name: 'Login',
                query: {redirect: to.fullPath}
              })
            })
        }
      })
  }
}



