import {HTTP, bitappPath} from '@/services/common'
import authservice from '@/services/authservice'

export default {
  getProjects: function () {
    return HTTP.get(
      bitappPath + '/projects'
    )
  },
  getAllProjects: function () {
    return HTTP.get(
      bitappPath + '/projects',
      {
        headers: authservice.getAuthorizationHeader()
      }
    )
  },
  getProject: function (id) {
    return HTTP.get(
      bitappPath + '/project/' + id,
      {
        headers: authservice.getAuthorizationHeader()
      }
    )
  },
  createProject: function (project) {
    return HTTP.post(
      bitappPath + '/project/new',
      project,
      {
        headers: authservice.getAuthorizationHeader()
      }
    )
  },
  updateProject: function (project) {
    return HTTP.put(
      bitappPath + '/project/' + project.id,
      project,
      {
        headers: authservice.getAuthorizationHeader()
      }
    )
  },
  deleteProject: function (project) {
    return HTTP.delete(
      bitappPath + '/project/' + project.id,
      {
        headers: authservice.getAuthorizationHeader()
      }
    )
  }
}



