import axios from 'axios'
import {eventbus} from '@/assets/js/eventbus'

export const apiUrl = process.env.NODE_ENV === 'production' ? 'http://bitspatial.com' : 'http://localhost:5000'
export const bitappPath = '/api/v1'

export const HTTP = axios.create({
  baseURL: apiUrl
})

HTTP.interceptors.response.use(response => {
  return response
}, error => {
  if (error.response.status === 401) {
    eventbus.$emit('errors:401', error)
    console.log('errors:401')
    return Promise.reject(error)
  } else if (error.response.status === 422) {
    eventbus.$emit('errors:422', error)
    console.log('errors:422')
    return Promise.reject(error)
  } else {
    eventbus.$emit('error', error)
    console.log('error', error)
    return Promise.reject(error)
  }
})



