import Vue from 'vue'
import Router from 'vue-router'
import HomeView from '@/components/HomeView'
import authservice from '@/services/authservice'

// const Projects = () => import('@/components/Projects') // this is supposed to do the async component loading.
// const ProjectCard = () => import('@/components/ProjectCard')  // this is supposed to do the async component loading.

const Authorized = () => import('@/components/admin/Authorized')
const Admin = () => import('@/components/admin/Admin')
const Login = () => import('@/components/auth/Login')
const ProjectEdit = () => import('@/components/admin/ProjectEdit')

const Projects = () => import('@/components/Projects')
const Project = () => import('@/components/Project')
const Background = () => import('@/components/Background')

const HttpErrorView = () => import('@/components/404')

Vue.use(Router)

const router = new Router({
  mode: 'history',  // no way to add catch all re-route back to index on dreamhost. lame.
  routes: [
    {
      path: '/',
      name: 'HomeView',
      component: HomeView,
      redirect: {name: 'Background'},
      children: [
        {
          path: '/projects/',
          name: 'Projects',
          component: Projects
        },
        {
          path: '/project/:project_id_prop/',
          props: true,
          name: 'Project',
          component: Project
        },
        {
          path: '/background/',
          name: 'Background',
          component: Background
        }
      ]
    },
    {
      path: '/',
      name: 'Authorized',
      component: Authorized,
      children: [
        {
          path: '/project/new',
          props: {
            project_id_prop: null
          },
          name: 'ProjectCreate',
          component: ProjectEdit,
          meta: {authRequired: true}
        },
        {
          path: '/project/:project_id_prop/edit',
          props: true,
          name: 'ProjectEdit',
          component: ProjectEdit,
          meta: {authRequired: true}
        },
        {
          path: '/admin',
          name: 'Admin',
          component: Admin,
          meta: {authRequired: true}
        },
        {
          path: '/login',
          name: 'Login',
          component: Login
        }
      ]
    },
    {
      path: '*',
      props: {
        iconCode: 'info',
        httpCode: '404',
        message: 'Huh.'
      },
      component: HttpErrorView,
      name: '404-view'
    }
  ]
})
router.beforeEach((to, from, next) => {
  if (to.matched.some(record => record.meta.authRequired)) {
    // use auth guard to check token status/validity
    // authservice.authGuard(to, from, next)

    // Now instead we just check if tokens exist and permit navigation. The ajax
    // error responses will force logins instead
    if (authservice.hasTokens()) {
      next()
    } else {
      next({
        name: 'Login',
        query: {redirect: to.fullPath}
      })
    }
  } else {
    next() // just call next if no auth is required.
  }
})

export default router



