<template>
  <v-container grid-list-lg>
    <v-layout row wrap>
      <v-flex xs12 v-for="project in projects" :key="projects[project]">
        <project-card :project="project"></project-card>
      </v-flex>
    </v-layout>
  </v-container>
</template>
<script>
  import postservice from '@/services/postservice'
  import ProjectCard from '@/components/ProjectCard'
  export default {
    components: {
      ProjectCard
    },
    data () {
      return {
        projects: []
      }
    },
    created () {
      postservice.getProjects().then((response) => {
        this.projects = response.data
      })
    }
  }
</script>



